  let modal = false;

  let itemsPerPage = 12;
  let currentPage = 1;
  $: visibleItems = custom_items.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );
</script>

<div class="content w-full h-full bg-black" style="overflow:auto;">
<div class="text-white text-4xl flex justify-center justify-items-center content-center content-items-center flex-col w-full">
<p class="justify-center content-center flex">Mutilate-a-Doll 2 (Custom Items list)</p>
<div class="justify-center content-center flex content bg-black flex content-items-center content-self-center align-center align-items-center flex-row">
  <button class="button-page justify-center h-10 text-lg m-5" on:click={() => { currentPage--; }}>Previous page</button>
  <button class="button-page justify-center h-10 text-lg  m-5" on:click={() => { currentPage++; }}>Next page</button>
</div>
</div>
  <div class="grid grid-cols-4 gap-10">
  {#each visibleItems as item}
    <Card {...item} on:click={() => {
      modal = true;
      setTimeout(function() {
          modal = false;
        }, 5000);
       const textToCopy = `${item.content}`;
      navigator.clipboard.writeText(textToCopy)
      .then(() => {
        console.log("Text copied to clipboard:", textToCopy);
        
      })
      .catch(error => {
        console.error("Error copying text to clipboard:", error);
      });
    }}/>
  {/each}
  </div>
</div>
{#if modal === true}
  <div style="color:#cecece;border-radius:10px;border-color:#333333;border-width:1px;border-style:solid;background-color:rgb(30 30 30 / 88%);font-weight:bolder;margin:5px 30%;width:50%;transition: transform 1s ease-in-out;" class="popout fixed text-white flex content-center justify-center flex-row top-0 fixed">COPIED</div>
{/if}
<style>
  .popout {
    animation: popout 0.5s ease;
  }

  .content::-webkit-scrollbar {
    width: 4px;
  }

  .content::-webkit-scrollbar-thumb {
    color: #222222;
    background: #222222;
  }

  .content::-webkit-scrollbar-track {
    background: #333333;
  }

  @keyframes popout {
    0% {
      transform: translate(0px, -31px);
    }
    50% {
      transform: translate(0px, 30px);
    }
    100% {
      transform: translate(0px, 0px);
    }
  }

  .button-page {
    background: #151515;
    border-style: solid;
    border-color: #333333;
    border-width: 4px;
    border-radius: 245px;
    width: 20%;
    transition: all 0.5s ease;
  }

  .button-page:active {
    transition: all 0s ease;
    box-shadow: inset 0 0 25px 5px #2ba52b;
    border-color: #2ba52b;
    border-width: 6px;
  }

  .button-page:hover {
    border-color: #2b6ea5;
    border-width: 4px;
  }
</style>

